.carousel .slide {
  color: black;
  width: fit-content;
  max-width: 100%;
  height: max-content;
  max-height: 100%;
  border-radius: 15px;
  padding: 20px;
}

.carousel .slide img {
  width: 139px !important;
  max-width: 100%;
  border-radius: 50%;
  box-shadow: 0px 0px 10px rgb(0, 0, 0);
}

.myCarousel {
  box-shadow: 0px 0px 10px rgb(0, 0, 0);
  border-radius: 15px;
  background-color: rgba(249, 248, 248, 0.808);
  margin-top: -6%;
  width: 44%;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  border: 1px solid #ddd;
  height: 330px;
  max-height: 100%;
}

.carousel .control-dots {
  padding-left: 5px !important;
  outline: 0;
  margin-top: 20px;
  color: rgba(183, 0, 255, 0.738);
  bottom: 5% !important;
}

.myCarousel h3 {
  color: #222;
  font-weight: 100;
  letter-spacing: 0.2px;
  margin-bottom: 4px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 17px;
}

.myCarousel h4 {
  text-transform: uppercase;
  margin-top: 0;
  padding-top: 0;
  font-weight: 500;
  color: rgb(235, 14, 228);
  font-size: 14px;
}

.myCarousel p {
  font-weight: 100 !important;
  line-height: 29px !important;
  color: #222;
  font-size: 13px;
  font-family: sans-serif;
  max-height: 67px;
}

.myCarousel p:before {
  content: "";
  color: rgb(235, 14, 228);
  font-size: 20px;
  font-family: monospace;
  font-weight: 100;
}

.myCarousel p:after {
  content: "";
  color: rgb(235, 14, 228);
  font-size: 20px;
  font-family: monospace;
  font-weight: 100;
  line-height: 0;
}

.carousel .control-dots .dot {
  box-shadow: none !important;
  background: #c008ca !important;
  outline: 0;
}

.carousel.carousel-slider .control-arrow {
  background: #000 !important;
  height: 80px !important;
  position: absolute;
  display: none;
  border-radius: 30px;
  top: 35% !important;
}

.App {
  text-align: center;
}

@media (max-width: 576px) {
  .carousel-root {
    outline: 0;
    width: 93% !important;
    margin: auto !important;
  }

  .carousel.carousel-slider .control-arrow {
    display: none !important;
  }

  .myCarousel {
    background-color: rgba(249, 248, 248, 0.808);
    margin-top: -9%;
    width: 98%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    padding-bottom: 12.5%;
    padding-left: 5%;
    padding-right: 5%;
    border: 1px solid #ddd;
    height: 350px;
  }

  .carousel .slide img {
