/* BEGINNING OF SMALL MOBILE STYLING */

@media only screen and (max-width: 600px) {
  .landing-page-heading-after-auth {
    margin-top: 3%;
    margin-left: 5%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }

  .landing-page-heading-before-auth {
    margin-top: 9%;
    margin-left: 5%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }

  #demo-button {
    margin: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
  }

  h1 {
    margin-bottom: -20px;
    font-size: 55px !important;
    color: rgb(179, 15, 194);
    text-shadow: 0 1px 2px rgb(255, 255, 255), 0 1px 2px;
    margin-top: 15px;
  }

  h2 {
    margin-top: 35px;
    font-size: 30px;
    color: white;
    margin-bottom: -10px;
  }

  h3 {
    margin-top: 25px;
    font-size: 20px;
    color: white;
  }

  .home-container {
    margin-top: 15px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    margin: auto;
  }

  .developer-word {
    color: rgb(226, 92, 239);
  }

  .device-mockups {
    position: relative;
    width: 1300px;
    max-width: 100%;
    margin: auto;
    margin-left: 200px;
    display: none;
  }

  .fade-in-image {
    animation: fadeIn 5s;
    -webkit-animation: fadeIn 5s;
    -moz-animation: fadeIn 5s;
    -o-animation: fadeIn 5s;
    -ms-animation: fadeIn 5s;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  hr {
    color: white;
  }

  .about-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    float: left;
    margin-top: 50px;
  }

  .testimonials-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 100%;
    margin: auto;
    display: flex;
    margin-top: 150px;
  }

  .contact-container {
    width: 100%;
    max-width: 100%;
    margin: auto;
    margin-bottom: -200px;
    margin-top: 100px;
  }

  .footer-container {
    width: 100%;
    max-width: 100%;
    margin: auto;
  }
}

/* END OF MOBILE STYLING */

